import React from 'react'
import { Sidebar, Menu, MenuItem, SubMenu, useProSidebar } from 'react-pro-sidebar'
import { SlHome, SlMenu, SlWallet, SlLayers, SlBasket, SlBriefcase, SlChart, SlLogout } from 'react-icons/sl'
import { CiBank } from 'react-icons/ci'
import { Routes, Route, Link } from "react-router-dom"
//import { useState } from 'react'
import { Inicio } from '../pages/Inicio'
import { Divider } from '@mui/material'


const Barlat = () => {

  
  const { collapseSidebar } = useProSidebar();

  return (
    <div>
      <Sidebar>
        <Menu>
          <MenuItem icon={<SlMenu/>} component={<Link to="/"/>}
          onClick={()=> {collapseSidebar();}}>
          </MenuItem>
         
          <MenuItem icon={<SlHome/>} component={<Link to="/pages/inicio"/>}> Inicio </MenuItem>
          <SubMenu label="Articulos" icon={<SlWallet/>}>
            <MenuItem> Articulos </MenuItem>
            <MenuItem> Lista Precios </MenuItem>
            <MenuItem> Ajuste Inventarios </MenuItem>
          </SubMenu>
          <MenuItem icon={<CiBank/>}> Banca </MenuItem>
          <Divider/>
          <SubMenu label="Ventas" icon={<SlBasket/>}>
            <MenuItem> Clientes </MenuItem>
            <MenuItem> Estimaciones </MenuItem>
            <MenuItem> Órdenes de venta </MenuItem>
            <MenuItem> Facturas </MenuItem>
            <MenuItem> Pagos recibidos </MenuItem>
            <MenuItem> Facturas recurrentes </MenuItem>
            <MenuItem> Notas de crédito </MenuItem>
          </SubMenu>
          <SubMenu label="Compras" icon={<SlBriefcase/>} >
            <MenuItem> Proveedores </MenuItem>
            <MenuItem> Gastos </MenuItem>
            <MenuItem> Gastos recurrentes </MenuItem>
            <MenuItem> Órdenes de compra </MenuItem>
            <MenuItem> Facturas proovedor </MenuItem>
            <MenuItem> Pagos realizados </MenuItem>
            <MenuItem> Facturas de proveedor recurrentes </MenuItem>
            <MenuItem> Créditos del proveedor </MenuItem>
          </SubMenu>
          <SubMenu label="Contable" icon={<SlLayers/>}>
            <MenuItem> Diarios manuales </MenuItem>
            <MenuItem> Actualización masiva </MenuItem>
            <MenuItem> Ajustes de la moneda </MenuItem>
            <MenuItem> Catálogo de cuentas </MenuItem>
            <MenuItem> Presupuestos </MenuItem>
            <MenuItem> Bloqueo de transacciones </MenuItem>
            <MenuItem> Facturas de proveedor recurrentes </MenuItem>
            <MenuItem> Créditos del proveedor </MenuItem>
          </SubMenu>
          <MenuItem icon={<SlChart/>}> Informes </MenuItem>
          <Divider/>
          <MenuItem icon={<SlLogout/>}> Salir </MenuItem>
        </Menu>
      </Sidebar>
      <section>
        <Routes>
          <Route path="/pages/" element={<Inicio />}/>
        </Routes>
      </section>
    </div>
  );
};

export default Barlat