import React from 'react'
import { Link } from 'react-router-dom'


const Navbar = () =>  {
  return (
    
    <nav className="navbar navbar-expand-lg bg-dark">
        
    <div className="container-fluid">
    
    <div className="d-flex align-items-start">
      <Link className="navbar-brand text-light" to="/">Grupo Debate</Link>
      <form className="d-flex"  role="search">
          <input className="form-control  me-2" type="search" placeholder="Buscar" aria-label="Search"/>
          <button className="btn btn-outline-primary text-light" type="submit">Buscar</button>
      </form>
    </div>  
      <div className="d-flex flex-row-reverse">
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
                <Link className="nav-link active text-light" aria-current="page" to="/paginas/home">
                  Salir</Link>
            </li>
            {/*<li className="nav-item dropdown">
                <Link className="nav-link dropdown-toggle text-light" to="/" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                Contable
                </Link>
                <ul className="dropdown-menu text-light">
                <li><Link className="dropdown-item" to="/paginas/cuentas">Catálogo Cuentas</Link></li>
                <li><Link className="dropdown-item" to="/paginas/polizas">Pólizas</Link></li>
                </ul>
            </li>
            <li className="nav-item">
                <Link className="nav-link disabled text-light">Desactivado</Link>
  </li>*/}
            </ul>       
        </div>
      </div>
    </div>
   </nav>
 
  )
}

export default Navbar